<template>
  <div id="wangeditor" class="wangeditor">
    <div ref="editorElem" style="text-align:left"></div>
  </div>
</template>
<script>
  import E from 'wangeditor';

  export default {
    name: 'editorElem',
    data() {
      return {
        editor: null,
        editorContent: '',
      };
    },
    props: ['catchData', 'content'],    // 
    watch: {
      content() {
        this.editor.txt.html(this.content);
      },
    },
    mounted() {
      this.editor = new E(this.$refs.editorElem);
      this.editor.customConfig.onchange = (html) => {
        this.editorContent = html;
        this.catchData(this.editorContent); // htmlcatchData
      };
      this.editor.customConfig.uploadImgServer = '';
      this.editor.customConfig.uploadFileName = '';
      this.editor.customConfig.menus = [          // 
        'head',  // 
        'bold',  // 
        'fontSize',  // 
        'fontName',  // 
        'italic',  // 
        'underline',  // 
        'strikeThrough',  // 
        'foreColor',  // 
        'backColor',  // 
        'link',  // 
        'list',  // 
        'justify',  // 
        'quote',  // 
        'emoticon',  // 
        // 'image',  // 
        'table',  // 
        'code',  // 
        'undo',  // 
        'redo',  // 
      ];
      // 
      this.editor.customConfig.uploadImgHooks = {
        // eslint-disable-next-line no-unused-vars
        before(xhr, editor, files) {
        // 
        // xhr  XMLHttpRequst editor files 

        //  {prevent: true, msg: 'xxxx'} 
        // return {
        //     prevent: true,
        //     msg: ''
        // }
        },
        success(xhr, editor, result) {
        // 
        // xhr  XMLHttpRequst editor result 
          this.imgUrl = Object.values(result.data).toString();
        },
        // eslint-disable-next-line no-unused-vars
        fail(xhr, editor, result) {
        // 
        // xhr  XMLHttpRequst editor result 
        },
        // eslint-disable-next-line no-unused-vars
        error(xhr, editor) {
        // 
        // xhr  XMLHttpRequst editor 
        },
        // eslint-disable-next-line no-unused-vars
        timeout(xhr, editor) {
        // 
        // xhr  XMLHttpRequst editor 
        },

        //  {errno:0, data: [...]} 
        //  JSON 
        // eslint-disable-next-line no-unused-vars
        customInsert(insertImg, result, editor) {
        // 
        // insertImg editor result 

        //  {url:'....'} 
          const url = Object.values(result.data);     // result.data
          JSON.stringify(url);   // JSON
          insertImg(url);
        // result  JSON 
        },
      };

      this.editor.create();     // 
      if (!this.content) {
        this.editor.txt.html('');
      }
    },
  };
</script>
<style scoped>
  .wangeditor {
    background-color: hsla(0,0%,100%,.7);
  }
</style>



